import React,{useState,useEffect} from 'react';
import { Row, Col, Layout, Typography,Button,Space, Switch } from 'antd';
import { LeftOutlined,InfoCircleOutlined } from '@ant-design/icons';
import {useHistory} from 'react-router-dom';





export const EditProfile:React.FC = () => {
    const [resize,setResize] = useState(false);
    const resizeScreen = () => {
        if(window.innerWidth <= 500){
            setResize(true);
        }else{
            setResize(false);
        }
    }
    useEffect(()=>{
        resizeScreen();
        window.addEventListener('resize', resizeScreen);
        return () => {
            window.removeEventListener('resize',resizeScreen);
        }
    },[])
    const {Content}  = Layout;
    const {Title,Paragraph} = Typography;
    const history = useHistory()
    const goBack = () => {
        history.goBack();
    }
    const sbmeditbtn = (event:React.FormEvent) => {
        event.preventDefault();
    }
    return (
        <Layout className='bg-white'>
            <Content className='padding-top-1 padding-left-1 padding-right-1'>
                <Row justify='center'>
                    <Col xxl={24} xl={24} sm={24} xs={24} style={{textAlign:'start'}}>
                        <Button icon={<LeftOutlined />} className='dark-blue body-line-normal-w-200' type='text' onClick={goBack} >{resize === false ?'Back':'Edit Profile'}</Button>
                    </Col>
                    <Col xxl={24} style={{textAlign:'center'}} className="padding-top-2">
                        <Space direction='vertical'>
                            {resize === false ?
                                <Title className='medium-line-medium-w-500 dark-blue padding-bottom-2'>
                                    Edit Profile
                                </Title> : null
}
                                <form onSubmit={sbmeditbtn}>
                                    <Space direction='vertical'size={21}>
                                    <div className="group">
                                        <input type="text" required />
                                        <label>First name</label>
                                    </div>
                                    <div className="group">
                                        <input type="text" required />
                                        <label>Last name</label>
                                    </div>
                                    <div className="group">
                                        <input type="text"  required/>
                                        <label>Email</label>
                                    </div>
                                    <div className="group">
                                        <input type="text"  required/>
                                        <label>Phone number</label>
                                    </div>
                                    <div className="group">
                                        <input type="text" required />
                                        <label>Address</label>
                                    </div>
                                    <div className="group">
                                        <input type="text" required/>
                                        <label>Country</label>
                                    </div>
                                    <div className="group">
                                        <input type="text"  required/>
                                        <label>Resident country</label>
                                    </div>
                                    <div className="group">
                                        <input type="text" required/>
                                        <label>Personal code</label>
                                    </div>
                                    <Row justify='space-between' className='padding-right-2 padding-left-2 padding-top-1 padding-bottom-1'>
                                        <Paragraph  className='dark-blue body-line-normal-w-300'>
                                            PEP
                                            <InfoCircleOutlined className='offset-left-1'/>
                                        </Paragraph>
                                       
                                        <Switch />
                                    </Row>
                                    <button type='submit' className='requestbtn'>Save changes</button>
                                    
                                    </Space>
                                </form>
                        </Space>
                    </Col>
                </Row>
            </Content>
        </Layout>
    )
}